import { Component, ViewChild, AfterViewInit, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { CollaborationService } from '../../services/collaboration.service';
import { DataService } from '../../services/data.service';
import * as ace from 'ace-builds';
import 'ace-builds/src-min-noconflict/theme-eclipse';
//import * as ace from 'brace';
//import 'brace/theme/eclipse';
//import 'brace/mode/java';
//import 'brace/mode/python'
//import 'brace/mode/c_cpp'
//import { AceEditorComponent } from '../../../../node_modules/ng2-ace-editor';
//declare var ace: any;
@Component({
  selector: 'app-editor',
  templateUrl: './editor.component.html',
  styleUrls: ['./editor.component.css']
})
export class EditorComponent implements OnInit {

  editor: any;
  sessionId: string;
  output: string = '';
  language: string = "Java";
  languages: string[] = ['Java', 'Python', 'C++'];
  defaultContent = { 'Java': `public class Example {
      public static void main(String[] args) {
        // Type your Java code here
      }
    }`,
      'Python': `class Solution:
      def example(self):
        # Write your python code here
      def main():
        Solution().example()
      if __name__ == "__main__":
        main()`,
      'C++': `#include <iostream>
      class ClassExample {
      public:
        void run() {
        // Write your C++ code here
        }        
      };
      int main() { 
        // This is the main entry point of a program.
      }`
    };
  constructor(private collaboration: CollaborationService,
              private data: DataService,
              private route: ActivatedRoute ) { }

  ngOnInit() {
      this.route.params
      .subscribe(params => {
        this.sessionId = params['id'];
        this.initEditor();      
      });
      
  }
  initEditor(): void {
    this.editor = ace.edit('editor');
    this.editor.setTheme('ace/theme/eclipse');
    //this.editor.getSession().setMode('ace/mode/java');
    //this.editor.setValue(this.defaultContent['Java']);
    this.editor.$blockScrolling = Infinity;
    this.resetEditor();

    this.collaboration.init(this.sessionId, this.editor);
    this.editor.lastAppliedChange = null;

    // regist change callbacks
    this.editor.on('change', (e) => {
      //console.log('editor change: ' + JSON.stringify(e));
      if (this.editor.lastAppliedChange != e) {
        //console.log('start to call collaboration serverice')
        this.collaboration.change(JSON.stringify(e));
      }
    });

    this.editor.getSession().getSelection().on('changeCursor', () => {
      const cursor = this.editor.getSession().getSelection().getCursor();
      //console.log('cursor from log from client ' + JSON.stringify(cursor));
      this.collaboration.cursorMove(JSON.stringify(cursor));
    });

    this.collaboration.restoreBuffer();
  }
  resetEditor(): void {
    //console.log('resetEditor')
    this.editor.setValue(this.defaultContent[this.language]);
    this.editor.getSession().setMode('ace/mode/' + this.language.toLowerCase());
    this.output = '';
  };
  setLanguage(language: string): void {
    this.language = language;
    this.resetEditor();
  };

  submit(): void {
    const userCodes = this.editor.getValue();
    //console.log(userCodes);
    const data = {
      userCodes: userCodes,
      lang: this.language.toLowerCase()
    };
    this.data.buildAndRun(data)
    .subscribe(res => {
      //console.log(executeResponse);
      this.output = res.text});
  };
}
