import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse, HttpHeaders } from '@angular/common/http';
import { BehaviorSubject } from 'rxjs';
import { Observable } from 'rxjs';
import { catchError } from 'rxjs/operators';

import { Problem } from '../models/problem.model';
//import { PROBLEMS } from '../mock-problems';


@Injectable({
  providedIn: 'root'
})
export class DataService {
  private _problemSource = new BehaviorSubject<Problem[]>([]);
  constructor(private http: HttpClient) { }

  getProblems(): Observable<Problem[]>{
    //return this.problems;
    this.http.get('api/v1/problems')
    .pipe(catchError(this.handleError))
    .subscribe((res: Problem[])  => this._problemSource.next(res));
    return this._problemSource.asObservable();

  }

  getProblem(id: number): Observable<Problem> {
    //return this.problems.find(problem => problem.id === id);
    return this.http.get<Problem>(`api/v1/problems/${id}`)
    .pipe(catchError(this.handleError));
  }

  addProblem(problem: Problem): Observable<Problem> {
    // problem.id = this.problems.length + 1;
    // this.problems.push(problem);
    const httpOptions = {headers: new HttpHeaders({'content-type':'application/json'})};
    return this.http.post('/api/v1/problems', problem, httpOptions)
    .pipe(catchError(this.handleError));
  }

  private handleError(error: any): Promise<any> {
    console.error('an error occurred', error);
    return Promise.reject(error.body || error);
  }

  buildAndRun(data): Observable<any>{
    const httpOptions = {headers: new HttpHeaders({'content-type': 'application/json'})};
    return this.http.post('/api/v1/build_and_run', data, httpOptions)
    .pipe(catchError(this.handleError));

    // .toPromise()
    // .then((res: Response) => {
    //   console.log(res.body);
    //   return res.json();
    // })
    // .catch(this.handleError);
  }
}
